import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { AnnotationService } from '../annotation/annotation.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-review-screen',
  templateUrl: './review-screen.component.html',
  styleUrls: ['./review-screen.component.css']
})
export class ReviewScreenComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  search:string;
  privilege:any;
  articles:any;
  constructor(private service:AnnotationService,
    private location: Location,
    private router:Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    this.privilege=localStorage.getItem("privilege");
    var obj = {"page":this.currPage,"user_id":+localStorage.getItem('user_id'),"token":localStorage.getItem("token")};
    this.service.fetchAll(JSON.stringify(obj)).subscribe(
      res=>{
        console.log(res);
        this.articles = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );
  }
  goBack() {
    this.location.back();
  }
  review(id){
    localStorage.setItem('total_page',"0");
    localStorage.setItem('page',"0");
    localStorage.setItem('article_id',id);
    localStorage.setItem('review',"1");
    this.router.navigateByUrl("/annotation");
  }
  filter(val){
    this.search=val;
  }

}
