import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { PriceService } from '../price.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-price',
  templateUrl: './price.component.html',
  styleUrls: ['./price.component.css']
})
export class PriceComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  prices:any;
  addPrice = new FormGroup({
    price_value: new FormControl("")
  });
  editForm = new FormGroup({
    price_value_id:new FormControl(""),
    price_value : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:PriceService, private toastr: ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    let token = localStorage.getItem('token');
    var body = {"page":+this.currPage,"is_null":"NULL","token":localStorage.getItem("token")};
    let tokenJson = JSON.stringify(body);
    this.service.fetchPrice(body).subscribe(
      res => {
        this.prices = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );

  }
  onSubmit(){
    console.log(this.addPrice.value);
    this.addPrice.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addPrice.value);
    this.service.addPrice(bodyJson).subscribe(
      res => {
        if(res['price_value_id']){
          this.toastr.success('','Saved Succesfully',  {
            timeOut: 5000
          });
        }
        else{
          this.toastr.error('','Error Saving',  {
            timeOut: 5000
          });
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['price_value_id'].setValue(region.price_value_id);
    this.editForm.controls['price_value'].setValue(region.price_value);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deletePrice(body).subscribe(
      res=>{
        location.reload();
        if(res=="success"){
          this.toastr.success('', 'Updated Successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Error while updating', {
            timeOut: 5000
          });
        }
      }
    );
  }

}
