import { Component, OnInit } from '@angular/core';
import { SupplyService } from '../supply.service';
import { FormControl, FormGroup } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-supply',
  templateUrl: './supply.component.html',
  styleUrls: ['./supply.component.css']
})
export class SupplyComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  supplies:any;
  addSupply = new FormGroup({
    supply_value: new FormControl("")
  });
  editForm = new FormGroup({
    supply_value_id:new FormControl(""),
    supply_value : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:SupplyService, private toastr: ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    if(!localStorage.getItem("token")){
      location.href="";
    }
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    let token = localStorage.getItem('token');
    var body = {"page":+this.currPage,"is_null":"NULL","token":localStorage.getItem("token")};
    let tokenJson = JSON.stringify(body);
    this.service.fetchSupply(body).subscribe(
      res => {
        this.supplies = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );

  }
  onSubmit(){
    console.log(this.addSupply.value);
    this.addSupply.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addSupply.value);
    this.service.addSupply(bodyJson).subscribe(
      res => {
        if(res['supply_value_id']){
          this.toastr.success('','Saved Succesfully',  {
            timeOut: 5000
          });
        }
        else{
          this.toastr.error('','Error Saving',  {
            timeOut: 5000
          });
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['supply_value_id'].setValue(region.supply_value_id);
    this.editForm.controls['supply_value'].setValue(region.supply_value);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteSupply(body).subscribe(
      res=>{
        location.reload();
        if(res=="success"){
          this.toastr.success('', 'Updated Successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Error while updating', {
            timeOut: 5000
          });
        }
      }
    );
  }

}
