import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { DemandService } from '../demand.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-demand',
  templateUrl: './demand.component.html',
  styleUrls: ['./demand.component.css']
})
export class DemandComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  demands:any;
  addDemand = new FormGroup({
    demand_value: new FormControl("")
  });
  editForm = new FormGroup({
    demand_value_id:new FormControl(""),
    demand_value : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:DemandService, private toastr: ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    let token = localStorage.getItem('token');
    var body = {"page":+this.currPage,"is_null":"NULL","token":localStorage.getItem("token")};
    let tokenJson = JSON.stringify(body);
    this.service.fetchDemand(body).subscribe(
      res => {
        this.demands = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );

  }
  onSubmit(){
    console.log(this.addDemand.value);
    this.addDemand.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addDemand.value);
    this.service.addDemand(bodyJson).subscribe(
      res => {
        if(res['demand_value']){
          this.toastr.success('','Saved Succesfully',  {
            timeOut: 5000
          });
        }
        else{
          this.toastr.error('','Error Saving',  {
            timeOut: 5000
          });
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['demand_value_id'].setValue(region.demand_value_id);
    this.editForm.controls['demand_value'].setValue(region.demand_value);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteDemand(body).subscribe(
      res=>{
        location.reload();
        if(res=="success"){
          this.toastr.success('', 'Updated Successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Error while updating', {
            timeOut: 5000
          });
        }
      }
    );
  }

}
