import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { CategoryService } from '../category.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  categories:any;
  addCategory = new FormGroup({
    categories  : new FormControl("")
  });
  editForm = new FormGroup({
    category_id:new FormControl(""),
    categories : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:CategoryService, private toastr: ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    let token = localStorage.getItem('token');
    var body = {"page":+this.currPage,"is_null":"NULL","token":localStorage.getItem("token")};
    let tokenJson = JSON.stringify(body);
    this.service.fetchCategory(body).subscribe(
      res => {
        this.categories = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );

  }
  onSubmit(){
    console.log(this.addCategory.value);
    this.addCategory.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addCategory.value);
    this.service.addCategory(bodyJson).subscribe(
      res => {
        if(res['category_id']){
          this.toastr.success('','Saved Succesfully',  {
            timeOut: 5000
          });
        }
        else{
          this.toastr.error('','Error Saving',  {
            timeOut: 5000
          });
        }
        location.reload();
      }
    );
  }
  openModal(region){
    this.editForm.controls['category_id'].setValue(region.category_id);
    this.editForm.controls['categories'].setValue(region.categories);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteCategory(body).subscribe(
      res=>{
        location.reload();
        if(res=="success"){
          this.toastr.success('', 'Updated Successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Error while updating', {
            timeOut: 5000
          });
        }
      }
    );
  }

}
