import { Component, OnInit, Input } from '@angular/core';
import { AnnotationService } from '../annotation.service';
import { FormControl, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common'

@Component({
  selector: 'app-article-info',
  templateUrl: './article-info.component.html',
  styleUrls: ['./article-info.component.css']
})
export class ArticleInfoComponent implements OnInit {
  infoForm = new FormGroup({
    article_id:new FormControl(""),
    headline: new FormControl(""),
    owner: new FormControl(""),
    release_date: new FormControl(""),
    source: new FormControl(""),
    url: new FormControl(""),
  });
  page:number = 1;
  article: any;
  constructor(private service:AnnotationService,public datepipe: DatePipe) { }

  ngOnInit() {
        if(localStorage.getItem('article_id')!=""){
          this.infoForm.controls['article_id'].setValue(localStorage.getItem('article_id'));
        }
        if(localStorage.getItem('headline')!=""){
          this.infoForm.controls['headline'].setValue(localStorage.getItem('headline'));
        }
        if(localStorage.getItem('owner')!=""){
          this.infoForm.controls['owner'].setValue(localStorage.getItem('owner'));
        }
        if(localStorage.getItem('release_date')!=""){
          this.infoForm.controls['release_date'].setValue(this.datepipe.transform(localStorage.getItem('release_date'),'yyyy-MM-dd'));
        }
        if(localStorage.getItem('owner')!=""){
          this.infoForm.controls['owner'].setValue(localStorage.getItem('owner'));
        }
        if(localStorage.getItem('url')!=""){
          this.infoForm.controls['url'].setValue(localStorage.getItem('url'));
        }
    this.saveForm();
  }

  submit(e:any){
    this.saveForm();
  }
  ngOnDestroy(){
    this.saveForm();
  }

  saveForm(){
    localStorage.setItem("article_id",this.infoForm.value.article_id);
    localStorage.setItem("headline",this.infoForm.value.headline);
    localStorage.setItem("owner",this.infoForm.value.owner);
    localStorage.setItem("release_date",this.infoForm.value.release_date);
    localStorage.setItem("source",this.infoForm.value.source);
    localStorage.setItem("url",this.infoForm.value.url);
  }
}
