import { Component, OnInit, ElementRef } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { LoginService } from '../login/login.service';
import { UserAddService } from '../upload/user-add.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-add',
  templateUrl: './user-add.component.html',
  styleUrls: ['./user-add.component.css']
})
export class UserAddComponent implements OnInit {

  currPage:number;
  pages:any;
  count:any;
  search:string;
  userEdit:any;
  users:any;
  constructor(private service:LoginService,private user:UserAddService,private elRef: ElementRef,private toastr: ToastrService,private route: ActivatedRoute) { }
  signupForm = new FormGroup({
    username : new FormControl(""),
    password : new FormControl(""),
    phone : new FormControl(""),
    email : new FormControl(""),
    privilege : new FormControl("0")
  });

  editForm = new FormGroup({
    user_id:new FormControl(""),
    username : new FormControl(""),
    pass_key : new FormControl(""),
    phone : new FormControl(""),
    email : new FormControl(""),
    privilege : new FormControl("2"),
    status: new FormControl("")
  });

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    this.editForm.controls['status'].setValue("enabled");
    var token = {"page":+this.currPage,"token":localStorage.getItem("token")};
    var tokenJson = JSON.stringify(token);
    this.user.fetchUsers(tokenJson).subscribe(
      res=>{
        this.users=res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    )

  }
  onSubmit(){
    
    if(this.signupForm.controls['username'].value == "" || this.signupForm.controls['password'].value == "" || this.signupForm.controls['email'].value == "" || this.signupForm.controls['phone'].value == "" || this.signupForm.controls['privilege'].value <= 0){
      this.toastr.error('', 'Please fill on all fields', {
        timeOut: 5000
      });
    }else{
      this.signupForm.value.token=localStorage.getItem('token');
    let body = JSON.stringify(this.signupForm.value);
    console.log(body);
    this.service.signup(body).subscribe(
      data => {
        console.log(data)
        if(data=="successful"){
          this.toastr.success('', 'User added successfully', {
            timeOut: 5000
          });
        }else{
          this.toastr.error('', 'Failed to add user', {
            timeOut: 5000
          });
        }
        location.reload();
      }
      );
    }
    
  }

  openModal(user){
    this.editForm.controls['user_id'].setValue(user.user_id);
    this.editForm.controls['username'].setValue(user.username);
    this.editForm.controls['pass_key'].setValue(user.pass_key);
    this.editForm.controls['email'].setValue(user.email);
    this.editForm.controls['phone'].setValue(user.phone);
    this.editForm.controls['status'].setValue(user.status);
    this.editForm.controls['privilege'].setValue(user.privilege);
  }
  edit(){
    if(this.editForm.controls['username'].value != "" || this.editForm.controls['password'].value != "" || this.editForm.controls['email'].value != "" || this.editForm.controls['phone'].value != "" || this.editForm.controls['privilege'].value <= 0){
      this.toastr.error('', 'Please fill on all fields', {
        timeOut: 5000
      });
    }else{
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.user.update(body).subscribe(
      res=>{
        location.reload();
        // console.log(res);
      }
    );
    }
  }
}
