import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {BaseLayoutComponent} from './Layout/base-layout/base-layout.component';
import {PagesLayoutComponent} from './Layout/pages-layout/pages-layout.component';

// DEMO PAGES

// Dashboards


// Pages

import {ForgotPasswordBoxedComponent} from './DemoPages/UserPages/forgot-password-boxed/forgot-password-boxed.component';
import {LoginBoxedComponent} from './DemoPages/UserPages/login-boxed/login-boxed.component';
import {RegisterBoxedComponent} from './DemoPages/UserPages/register-boxed/register-boxed.component';

// Elements

import {StandardComponent} from './DemoPages/Elements/Buttons/standard/standard.component';
import {DropdownsComponent} from './DemoPages/Elements/dropdowns/dropdowns.component';
import {CardsComponent} from './DemoPages/Elements/cards/cards.component';
import {ListGroupsComponent} from './DemoPages/Elements/list-groups/list-groups.component';
import {TimelineComponent} from './DemoPages/Elements/timeline/timeline.component';
import {IconsComponent} from './DemoPages/Elements/icons/icons.component';

// Components

import {AccordionsComponent} from './DemoPages/Components/accordions/accordions.component';
import {TabsComponent} from './DemoPages/Components/tabs/tabs.component';
import {CarouselComponent} from './DemoPages/Components/carousel/carousel.component';
import {ModalsComponent} from './DemoPages/Components/modals/modals.component';
import {ProgressBarComponent} from './DemoPages/Components/progress-bar/progress-bar.component';
import {PaginationComponent} from './DemoPages/Components/pagination/pagination.component';
import {TooltipsPopoversComponent} from './DemoPages/Components/tooltips-popovers/tooltips-popovers.component';

// Tables

import {TablesMainComponent} from './DemoPages/Tables/tables-main/tables-main.component';

// Widgets

import {ChartBoxes3Component} from './DemoPages/Widgets/chart-boxes3/chart-boxes3.component';

// Forms Elements

import {ControlsComponent} from './DemoPages/Forms/Elements/controls/controls.component';
import {LayoutComponent} from './DemoPages/Forms/Elements/layout/layout.component';

// Charts

import { LoginComponent } from './annotation/login/login.component';
import { UploadComponent } from './annotation/upload/upload.component';
import { AnnotationComponent } from './annotation/annotation/annotation.component';
import { ReviewScreenComponent } from './annotation/review-screen/review-screen.component';
import { IndexComponent } from './annotation/index/index.component';
import { UserAddComponent } from './annotation/user-add/user-add.component';
import { RegionComponent } from './annotation/region/region.component';
import { CommodityComponent } from './annotation/commodity/commodity.component';
import { CategoryComponent } from './annotation/category/category.component';
import { SubCategoryComponent } from './annotation/sub-category/sub-category.component';
import { MovingFactorComponent } from './annotation/moving-factor/moving-factor.component';
import { FactorValueComponent } from './annotation/factor-value/factor-value.component';
import { PriceComponent } from './annotation/price/price.component';
import { SupplyComponent } from './annotation/supply/supply.component';
import { DemandComponent } from './annotation/demand/demand.component';
import { DisruptionComponent } from './annotation/disruption/disruption.component';
import { AnnotationBaseComponent } from './Layout/annotation-base/annotation-base.component';
import { AuthGuardService } from './auth-guard.service';

const routes: Routes = [
  
  
      // annotation
      
      {'path':'',component:LoginComponent},
      {
      path: '',
      component: AnnotationBaseComponent,
      children: [
  
  {'path':'upload',component:UploadComponent,canActivate: [AuthGuardService],},
  {'path':'annotation',component:AnnotationComponent,canActivate: [AuthGuardService],},
  {'path':'review/:page',component:ReviewScreenComponent,canActivate: [AuthGuardService],},
  {'path':'index',component:IndexComponent,canActivate: [AuthGuardService],},
  {'path':'users/:page',component:UserAddComponent,canActivate: [AuthGuardService],},
  {'path':'region/:page',component:RegionComponent,canActivate: [AuthGuardService],},
  {'path':'commodity/:page',component:CommodityComponent,canActivate: [AuthGuardService],},
  {'path':'category/:page',component:CategoryComponent,canActivate: [AuthGuardService],},
  {'path':'subCategory/:page',component:SubCategoryComponent,canActivate: [AuthGuardService],},
  {'path':'movingFactor/:page',component:MovingFactorComponent,canActivate: [AuthGuardService],},
  {'path':'factorValue/:page',component:FactorValueComponent,canActivate: [AuthGuardService],},
  {'path':'price/:page',component:PriceComponent,canActivate: [AuthGuardService],},
  {'path':'supply/:page',component:SupplyComponent,canActivate: [AuthGuardService],},
  {'path':'demand/:page',component:DemandComponent,canActivate: [AuthGuardService],},
  {'path':'disruption/:page',component:DisruptionComponent,canActivate: [AuthGuardService],},

      ]}
    ]


@NgModule({
  imports: [RouterModule.forRoot(routes,
    {
      scrollPositionRestoration: 'enabled',
      anchorScrolling: 'enabled',
    })],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
