import { Component, OnInit } from '@angular/core';
import {   FormGroup, FormControl } from '@angular/forms';
import { RegionService } from '../region.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-region',
  templateUrl: './region.component.html',
  styleUrls: ['./region.component.css']
})
export class RegionComponent implements OnInit {
  currPage:number;
  pages:any;
  count:any;
  regions:any;
  addRegion = new FormGroup({
    countries: new FormControl("")
  });
  editForm = new FormGroup({
    country_id:new FormControl(""),
    countries : new FormControl(""),
    status : new FormControl(""),
  });
  
  constructor(private service:RegionService, private toastr:ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currPage = +params['page'];
    });
    var body = {"page":+this.currPage,"is_null":"NULL","token":localStorage.getItem("token")};
    let tokenJson = JSON.stringify(body);
    this.service.fetchRegion(body).subscribe(
      res => {
        console.log(res);
        this.regions = res['data'];
        this.pages = Array(res['pages']).fill(1).map((x,i)=>i);
        this.count=+this.pages.length;
      }
    );

  }
  onSubmit(){
    if(!this.addRegion.value){
      this.toastr.error('', 'Please add value', {
        timeOut: 5000
      });
    }
    this.addRegion.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addRegion.value);
    this.service.addRegion(bodyJson).subscribe(
      res => {
        if(res['country_id']){
          this.toastr.success('','Saved Succesfully',  {
            timeOut: 5000
          });
        }
        else{
          this.toastr.error('','Error Saving',  {
            timeOut: 5000
          });
        }
        location.reload();
      }
    );
    
  }
  openModal(region){
    this.editForm.controls['country_id'].setValue(region.country_id);
    this.editForm.controls['countries'].setValue(region.countries);
    this.editForm.controls['status'].setValue(region.status);
  }
  edit(){
    if(!this.editForm.controls['countries'].value){
      this.toastr.error('', 'Please add value', {
        timeOut: 5000
      });
    }
    this.editForm.value.token = localStorage.getItem('token');
    var body = JSON.stringify(this.editForm.value);
    console.log(body);
    this.service.deleteRegion(body).subscribe(
      res=>{
        if(res=="success"){
          this.toastr.success('', 'Updated Successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Error while updating', {
            timeOut: 5000
          });
        }
        
      }
    );
  }

}
