import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'filter',
    pure: false
})
export class FilterPipe implements PipeTransform {
    transform(items: any[], filter: string): any {
        
        if (!items || !filter) {
            return items;
        }
        var key = [];
        key.push(Object.keys(items[0])[0]);
        key.push(Object.keys(items[0])[1]);
        var regex = new RegExp(filter.toLowerCase(), "gi");
        // filter items array, items which match and return true will be
        // kept, false will be filtered out
        switch (key[0]) {
            case 'countries':
                return items.filter(item => regex.test(item.countries.toString().toLowerCase()));
                break;
            case 'commodities':
                return items.filter(item => regex.test(item.commodities.toString().toLowerCase()));
                break;
            case 'categories':
                return items.filter(item => regex.test(item.categories.toString().toLowerCase()));

                break;
            
            case 'factor_value':
                return items.filter(item => regex.test(item.factor_value.toString().toLowerCase()));

                break;
            case 'price_value':
                return items.filter(item => regex.test(item.price_value.toString().toLowerCase()));

                break;
            
            case 'demand_value':
                return items.filter(item => regex.test(item.demand_value.toString().toLowerCase()));

                break;
            case 'sc_disruption_value':
                return items.filter(item => regex.test(item.sc_disruption_value.toString().toLowerCase()));

                break;
                
        
            default:
                switch (key[1]) {
                    case 'supply_value':
                        return items.filter(item => regex.test(item.supply_value.toString().toLowerCase()));
                        break;
                    case 'sub_categories':
                        return items.filter(item => regex.test(item.sub_categories.toString().toLowerCase()));

                        break;
                    case 'moving_factors':
                        return items.filter(item => regex.test(item.moving_factors.toString().toLowerCase()));

                        break;
                    default:
                        break;
                }

                break;
        }
        
    }
}
 