import { Component, OnInit, ViewChild } from '@angular/core';
import { CSVRecord } from './CSVModel'; 
import { UploadService } from './upload.service';
import { ToastrService } from 'ngx-toastr';
//https://www.c-sharpcorner.com/article/read-csv-file-in-angular-7/ - reference
@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  public records: any[] = [];  
  @ViewChild('csvReader') csvReader: any;  

  constructor(private service:UploadService,private toastr: ToastrService) { }

  ngOnInit() {
  }

  uploadListener($event: any): void {  
  
    let text = [];  
    let files = $event.srcElement.files;  
  
    if (this.isValidCSVFile(files[0])) {  
  
      let input = $event.target;  
      let reader = new FileReader();  
      reader.readAsText(input.files[0]);  
  
      reader.onload = () => {  
        let csvData = reader.result;  
        let csvRecordsArray = (csvData.toString()).split(/\r\n|\n/); 
  
        let headersRow = this.getHeaderArray(csvRecordsArray);  
  
        this.records = this.getDataRecordsArrayFromCSVFile(csvRecordsArray, headersRow.length);  
      };  
  
      reader.onerror = function () {  
        console.log('error is occured while reading file!');  
      };  
  
    } else {  
      alert("Please import valid .csv file.");  
      this.fileReset();  
    }  
  }  
  
  getDataRecordsArrayFromCSVFile(csvRecordsArray: any, headerLength: any) {  
    let csvArr = [];  
    for (let i = 1; i < csvRecordsArray.length; i++) {  
      let curruntRecord = (<string>csvRecordsArray[i]).substr(1,csvRecordsArray[i].length-2).split(',');  
      console.log(curruntRecord);
      if (curruntRecord.length == headerLength) {  
        let csvRecord: CSVRecord = new CSVRecord();  
        csvRecord.author = curruntRecord[0].trim();  
        csvRecord.description = curruntRecord[1].trim();  
        csvRecord.releaseDate = curruntRecord[2].trim();  
        csvRecord.title = curruntRecord[3].trim();  
        csvRecord.url = curruntRecord[4].trim();  
        csvArr.push(csvRecord);  
      }  
    }  
    csvArr.push(Number(localStorage.getItem("user_id")));
    if(!csvArr){
      this.toastr.error('', 'Upload failed', {
        timeOut: 5000
      });
    }else{
      this.service.upload(JSON.stringify(csvArr)).subscribe(
        res=>{
          if(res=="success"){
            this.toastr.success('', 'File uploaded successfully', {
              timeOut: 5000
            });
          }else{
            this.toastr.error('', 'Upload failed', {
              timeOut: 5000
            });
          }
        }
      );
    }
    // console.log(JSON.stringify(csvArr));
    
    return csvArr;  
  }  
  
  isValidCSVFile(file: any) {  
    return file.name.endsWith(".csv");  
  }  
  
  getHeaderArray(csvRecordsArr: any) {  
    let headers = (csvRecordsArr[0]).split(',');  
    let headerArray = [];  
    for (let j = 0; j < headers.length; j++) {  
      headerArray.push(headers[j]);  
    }  
    return headerArray;  
  }  
  
  fileReset() {  
    this.csvReader.nativeElement.value = "";  
    this.records = [];  
  }  
}  

