import { Component, OnInit } from '@angular/core';
import { AnnotationService } from './annotation.service';
import { RegionService } from '../region.service';
import { CommodityService } from '../commodity.service';
import { CategoryService } from '../category.service';
import { SubCategoryService } from '../sub-category.service';
import { FactorValueService } from '../factor-value.service';
import { MovingFactorService } from '../moving-factor.service';
import { FormGroup, FormControl, FormBuilder } from '@angular/forms';
import { PriceService } from '../price.service';
import { DemandService } from '../demand.service';
import { DisruptionService } from '../disruption.service';
import { SupplyService } from '../supply.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-annotation',
  templateUrl: './annotation.component.html',
  styleUrls: ['./annotation.component.css']
})
export class AnnotationComponent implements OnInit {

  regionSearch:string;
  commSearch:string;
  catSearch:string;
  subcatSearch:string;
  factSearch:string;
  mfactSearch:string;
  priceSearch:string;
  supplySearch:string;
  demandSearch:string;
  disrupSearch:string;

  mappings:any;
  mapObj ={
    article_id:0,
    token:"",
  };
  privilege:any;

  mark={
    article_id:0,token:""
  
  }
  object={
    user_id:0,
    article_id:0,
    country_id:0,
    commodity_id:0,
    category_id:0,
    subcategory_id:0,
    moving_factor_id:0,
    factor_value_id:0,
    price_value_id:0,
    supply_value_id:0,
    demand_value_id:0,
    sc_disruption_value_id:0,
    owner:"",
    release_date: "",
    source: "",
    url: "",
    headline: "",
    content: "",
    question: "",
    token:""
  }
  

  regionTab:any;
  commTab:any;
  catTab:any;
  sCatTab:any;
  mFactTab:any;
  factTab:any;
  priceTab:any;
  demandTab:any;
  supplyTab:any;
  disruptionTab:any;



  editMap = new FormGroup({
    mapping_id:new FormControl(""),
    user_id:new FormControl(""),
    article_id:new FormControl(""),
    country_id:new FormControl(""),
    commodity_id:new FormControl(""),
    category_id:new FormControl(""),
    subcategory_id:new FormControl(""),
    moving_factor_id:new FormControl(""),
    factor_value_id:new FormControl(""),
    price_value_id:new FormControl(""),
    supply_value_id:new FormControl(""),
    demand_value_id:new FormControl(""),
    sc_disruption_value_id:new FormControl(""),
    token:new FormControl(""),
  });

  qnForm= new FormGroup({
    question:new FormControl("")
  });
  paramsForm:FormGroup;
  regionData:any;
  commodityData:any;
  movingFactorData:any;
  factorValueData:any;
  categoryData:any;Doe
  subCategoryData:any;
  supplyData:any;
  priceData:any;
  demandData:any;
  disruptionData:any;

  article:any="";
  page:number;
  constructor(private service:AnnotationService,
    private regionService:RegionService,
    private commodityService:CommodityService,
    private categoryService:CategoryService,
    private subCategoryService:SubCategoryService,
    private factorValueService:FactorValueService,
    private movingFactorService:MovingFactorService,
    private priceService:PriceService,
    private demandService:DemandService,
    private disruptionService:DisruptionService,
    private supplyService:SupplyService,
    private fb:FormBuilder,
    private toastr: ToastrService

    ) { }

  ngOnInit() {

    this.privilege=localStorage.getItem("privilege");
    var token={"page":0,"is_null":"enabled","token":localStorage.getItem("token")};
    this.page=+localStorage.getItem('page');
    this.paramsForm = this.fb.group({
      country_id:"",
      commodity_id:"",
      category_id:"",
      sub_category_id:"",
      factor_value_id:"",
      moving_factor_id:"",
      supply_id:"",
      price_id:"",
      demand_id:"",
      disruption_id:"",

    });
    if(localStorage.getItem('review')=="1"){
      var body = {"article_id":Number(localStorage.getItem("article_id")),"token":localStorage.getItem("token")};
      this.service.fetchById(JSON.stringify(body)).subscribe(
            
        res=>{
          this.article=res;
        localStorage.setItem("article_id",this.article['article_id']);
        localStorage.setItem("headline",this.article['headline']);
        localStorage.setItem("owner",this.article['owner']);
        localStorage.setItem("release_date",this.article['release_date']);
        localStorage.setItem("source",this.article['source']);
        localStorage.setItem("url",this.article['url']);
        localStorage.setItem("content",this.article['content']);
        
        if(this.article['question']!=""){
          this.qnForm.controls['question'].setValue(this.article['question']);
        }
        }
      );
    }else{

      
    var obj = {"user_id":+localStorage.getItem("user_id"),"flag":this.page,"token":localStorage.getItem("token")};
    var user = {"user_id":+localStorage.getItem("user_id"),"token":localStorage.getItem("token")};
    this.service.fetch(JSON.stringify(obj)).subscribe(
      res=>{
        this.article=res;
        localStorage.setItem("article_id",this.article['article_id']);
        localStorage.setItem("headline",this.article['headline']);
        localStorage.setItem("owner",this.article['owner']);
        localStorage.setItem("release_date",this.article['release_date']);
        localStorage.setItem("source",this.article['source']);
        localStorage.setItem("url",this.article['url']);
        localStorage.setItem("content",this.article['content']);
        
        if(this.article['question']!=""){
          this.qnForm.controls['question'].setValue(this.article['question']);
        }
      }
    );
    
    this.service.count(JSON.stringify(user)).subscribe(
      res=>{
        var tot = res['count'];
        localStorage.setItem('total_page',String(tot['0']));
      }
    );
    }

    
    
    this.regionService.fetchRegion(JSON.stringify(token)).subscribe(
      res=>{
        this.regionData=res['data'];
    }
    );
    this.commodityService.fetchCommodity(JSON.stringify(token)).subscribe(
      res=>{
        this.commodityData=res['data'];
    }
    );
    this.categoryService.fetchCategory(JSON.stringify(token)).subscribe(
      res=>{
        this.categoryData=res['data'];
    }
    );
    this.subCategoryService.fetchSubCategory(JSON.stringify(token)).subscribe(
      res=>{
        this.subCategoryData=res['data'];
    }
    );
    this.factorValueService.fetchFactorValue(JSON.stringify(token)).subscribe(
      res=>{
        this.factorValueData=res['data'];
    }
    );
    this.movingFactorService.fetchMovingFactor(JSON.stringify(token)).subscribe(
      res=>{
        this.movingFactorData=res['data'];
    }
    );
    this.supplyService.fetchSupply(JSON.stringify(token)).subscribe(
       res=>{
        this.supplyData=res['data'];
    }
    );
    this.priceService.fetchPrice(JSON.stringify(token)).subscribe(
      res=>{
        this.priceData=res['data'];
    }
    );
    this.demandService.fetchDemand(JSON.stringify(token)).subscribe(
      res=>{
        this.demandData=res['data'];
    }
    );
    this.disruptionService.fetchDisruption(JSON.stringify(token)).subscribe(
      res=>{
        this.disruptionData=res['data'];
    }
    );
    this.saveform();
    this.mapObj.article_id=+localStorage.getItem("article_id");
    this.mapObj.token=localStorage.getItem("token");
    this.service.fetchMapping(JSON.stringify(this.mapObj)).subscribe(
      res=>{
        if(res['message']=="success"){
          this.mappings=res['data'];
        }
      }
    );
    
  }

  table(value,param){
    switch(param){
      case "region":{this.regionTab = value;}
      break;
      case "commodity":{this.commTab = value;}
      break;
      case "category":{this.catTab = value;}
      break;
      case "sub":{this.sCatTab = value;}
      break;
      case "mFactor":{this.mFactTab = value;}
      break;
      case "factor":{this.factTab = value;}
      break;
      case "price":{this.priceTab = value;}
      break;
      case "demand":{this.demandTab = value;}
      break;
      case "supply":{this.supplyTab = value;}
      break;
      case "disruption":{this.disruptionTab = value;}
      break;
    }
  }
  submit(e:any){
    this.saveform();
  }
  saveform(){
    localStorage.setItem("country_id",this.paramsForm.value.country_id);
    localStorage.setItem("commodity_id",this.paramsForm.value.commodity_id);
    localStorage.setItem("category_id",this.paramsForm.value.category_id);
    localStorage.setItem("sub_category_id",this.paramsForm.value.sub_category_id);
    localStorage.setItem("factor_value_id",this.paramsForm.value.factor_value_id);
    localStorage.setItem("moving_factor_id",this.paramsForm.value.moving_factor_id);
    localStorage.setItem("demand_id",this.paramsForm.value.demand_id);
    localStorage.setItem("supply_id",this.paramsForm.value.supply_id);
    localStorage.setItem("price_id",this.paramsForm.value.price_id);
    localStorage.setItem("disruption_id",this.paramsForm.value.disruption_id);
  }
  ngOnDestroy(){
    this.saveform();
  }

  submitArticle(){
    this.object.user_id = +localStorage.getItem("user_id");
    this.object.article_id = +localStorage.getItem("article_id");
    this.object.country_id = +localStorage.getItem("country_id");
    this.object.commodity_id = +localStorage.getItem("commodity_id");
    this.object.category_id = +localStorage.getItem("category_id");
    this.object.subcategory_id = +localStorage.getItem("sub_category_id");
    this.object.moving_factor_id = +localStorage.getItem("moving_factor_id");
    this.object.factor_value_id = +localStorage.getItem("factor_value_id");
    this.object.price_value_id = +localStorage.getItem("price_id");
    this.object.supply_value_id = +localStorage.getItem("supply_id");
    this.object.demand_value_id = +localStorage.getItem("demand_id");
    this.object.sc_disruption_value_id = +localStorage.getItem("disruption_id");
    this.object.owner = localStorage.getItem("owner");
    this.object.release_date = localStorage.getItem("release_date");
    this.object.source = localStorage.getItem("source");
    this.object.url = localStorage.getItem("url");
    this.object.headline = localStorage.getItem("headline");
    this.object.content = localStorage.getItem("content");
    this.object.question = "NULL";
    this.object.token = localStorage.getItem("token");
    if(this.mappings){
      console.log(this.mappings);
      this.service.save(JSON.stringify(this.object)).subscribe(
        res=>{
          if(res="success"){
            this.toastr.success('','Article Saved Succesfully',  {
              timeOut: 5000
            });
          }else{
              this.toastr.error('','Failed to save',  {
                timeOut: 5000
              });
            }
          
        }
      );
    }
    else {
      if(this.object.supply_value_id<=0 || this.object.subcategory_id<=0 || this.object.sc_disruption_value_id<=0 || this.object.price_value_id<=0 || this.object.moving_factor_id<=0 || this.object.factor_value_id<=0 || this.object.demand_value_id<=0 || this.object.country_id<=0 || this.object.commodity_id<=0 || this.object.category_id<=0 ){
      this.toastr.error('Please select values for every mapping attributes !','Failed to save',  {
        timeOut: 5000
      });
    }else{
    this.service.save(JSON.stringify(this.object)).subscribe(
      res=>{
        if(res="success"){
          this.toastr.success('','Article Saved Succesfully',  {
            timeOut: 5000
          });
        }else{
            this.toastr.error('','Failed to save',  {
              timeOut: 5000
            });
          }
        
      }
    );
    }
  }

    
  }

  markQn(){
    this.object.user_id = +localStorage.getItem("user_id");
    this.object.article_id = +localStorage.getItem("article_id");
    this.object.country_id = +localStorage.getItem("country_id");
    this.object.commodity_id = +localStorage.getItem("commodity_id");
    this.object.category_id = +localStorage.getItem("category_id");
    this.object.subcategory_id = +localStorage.getItem("subcategory_id");
    this.object.moving_factor_id = +localStorage.getItem("moving_factor_id");
    this.object.factor_value_id = +localStorage.getItem("factor_value_id");
    this.object.price_value_id = +localStorage.getItem("price_id");
    this.object.supply_value_id = +localStorage.getItem("supply_id");
    this.object.demand_value_id = +localStorage.getItem("demand_id");
    this.object.sc_disruption_value_id = +localStorage.getItem("disruption_id");
    this.object.owner = localStorage.getItem("owner");
    this.object.release_date = localStorage.getItem("release_date");
    this.object.source = localStorage.getItem("source");
    this.object.url = localStorage.getItem("url");
    this.object.headline = localStorage.getItem("headline");
    this.object.content = localStorage.getItem("content");
    this.object.question = this.qnForm.controls['question'].value;
    this.object.token = localStorage.getItem("token");
    if(this.object.question==""){
      this.toastr.error('', 'Add Question !', {
        timeOut: 5000
      });
    }else{
    this.service.save(JSON.stringify(this.object)).subscribe(
      res=>{
        console.log(res);
        if(res='success article only'){
          this.toastr.success('Article marked with question','Saved Succesfully',  {
            timeOut: 5000
          });
        }else{
          this.toastr.error('', 'Failed to save', {
            timeOut: 5000
          });
        }
      }
    );
    }
  }
  markIrr(){
    this.mark.article_id=+localStorage.getItem("article_id");
    this.mark.token=localStorage.getItem("token");
    this.service.markIrr(JSON.stringify(this.mark)).subscribe(
      res=>{
        if(res=="success"){
          this.toastr.success('','Article marked as irrelevant',  {
            timeOut: 5000
          });
        }else{
          this.toastr.error('', 'Failed to save', {
            timeOut: 5000
          });
        }
      }
    );
  }
  prev(){
    if(this.page>=1){
      this.page-=1;
      localStorage.setItem('page',String(this.page));
      location.reload();
    }

  }
  next(){
    var total = +localStorage.getItem("total_page");
    if(this.page<total){
      this.page+=1;
      localStorage.setItem('page',String(this.page));
      location.reload();
    }

  }

  openModal(user){
    this.editMap.controls['mapping_id'].setValue(user.mapping_id);
    this.editMap.controls['user_id'].setValue(user.user_id);
    this.editMap.controls['article_id'].setValue(localStorage.getItem('article_id'));
    this.editMap.controls['country_id'].setValue(user.country_id);
    this.editMap.controls['commodity_id'].setValue(user.commodity_id);
    this.editMap.controls['category_id'].setValue(user.category_id);
    this.editMap.controls['subcategory_id'].setValue(user.subcategory_id);
    this.editMap.controls['moving_factor_id'].setValue(user.moving_factor_id);
    this.editMap.controls['factor_value_id'].setValue(user.factor_value_id);
    this.editMap.controls['price_value_id'].setValue(user.price_value_id);
    this.editMap.controls['demand_value_id'].setValue(user.demand_value_id);
    this.editMap.controls['supply_value_id'].setValue(user.supply_value_id);
    this.editMap.controls['sc_disruption_value_id'].setValue(user.sc_disruption_value_id);
    this.editMap.controls['token'].setValue(localStorage.getItem('token'));
  }
  editMapping(){

    this.service.editMapping(JSON.stringify(this.editMap.value)).subscribe(
      res=>{
        if(res['message']=='Success'){
          this.toastr.success('', 'Edited successfully', {
            timeOut: 5000
          });
          location.reload();
        }else{
          this.toastr.error('', 'Failed to edit', {
            timeOut: 5000
          });
        }
      }
    );
  }
}







