import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'userfilter',
    pure: false
})
export class UserFilterPipe implements PipeTransform {
    transform(items: any[], filter: string): any {
        
        if (!items || !filter) {
            return items;
        }
        var regex = new RegExp(filter.toLowerCase(), "gi");
        // filter items array, items which match and return true will be
        // kept, false will be filtered out

        if(regex.test("admin")){
            return items.filter(item => item.privilege==1);
        }else if(regex.test("user")){
            return items.filter(item => item.privilege==2);
        }else{
            return items.filter(item => (
                regex.test(item.username.toLowerCase()) ||
                regex.test(item.email.toLowerCase()) ||
                regex.test(item.phone.toLowerCase())
            ));
        }

    }
}
 